@import url(https://fonts.googleapis.com/css?family=Amatic+SC|Coming+Soon|Fredoka+One|Montserrat&display=swap);
html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit;
  outline: none; }

body {
  margin: 0;
  overflow: hidden; }

.App {
  position: relative; }

.hud {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  padding: 2%; }

a {
  text-decoration: none; }

.btn-help {
  border: 3px solid #fff;
  background: #fce01a;
  box-shadow: 0 -2px 0 3px #f1d403 inset, 0 5px 5px rgba(65, 8, 8, 0.17), 0 15px rgba(255, 255, 255, 0.25) inset;
  display: inline-block;
  color: #f8feff;
  text-align: center;
  font-family: "Fredoka One";
  text-decoration: none;
  border-radius: 100%;
  height: 70px;
  width: 70px;
  line-height: 3.7rem;
  font-size: 3rem;
  margin: 2% 0 0 90%;
  cursor: pointer; }

.btn-start {
  border: 3px solid #fff;
  background: #f4863d;
  box-shadow: 0 -2px 0 3px #f2721d inset, 0 5px 5px rgba(65, 8, 8, 0.17), 0 15px rgba(255, 255, 255, 0.25) inset;
  display: inline-block;
  color: #f8feff;
  text-align: center;
  font-family: "Fredoka One";
  text-decoration: none;
  border-radius: 20px;
  height: 3rem;
  width: 15rem;
  line-height: 2.5rem;
  font-size: 1.4rem;
  cursor: pointer; }

.btn-newstart {
  border: 3px solid #fff;
  background: #8f8aef;
  box-shadow: 0 -2px 0 3px #605ad7 inset, 0 5px 5px rgba(65, 8, 8, 0.17), 0 15px rgba(255, 255, 255, 0.25) inset;
  display: inline-block;
  color: #f8feff;
  text-align: center;
  font-family: "Fredoka One";
  text-decoration: none;
  border-radius: 20px;
  height: 3rem;
  width: 15rem;
  line-height: 2.5rem;
  font-size: 1.4rem;
  cursor: pointer;
  margin-top: 1rem; }

.btn-back, .btn-play {
  border: 3px solid #fff;
  background: #5f59d0;
  box-shadow: 0 -2px 0 3px #726ceb inset, 0 5px 5px rgba(65, 8, 8, 0.17), 0 15px rgba(255, 255, 255, 0.25) inset;
  display: inline-block;
  color: #f8feff;
  text-align: center;
  font-family: "Fredoka One";
  text-decoration: none;
  border-radius: 20px;
  height: 3rem;
  width: 9rem;
  line-height: 2.5rem;
  font-size: 1.4rem;
  margin: 2%;
  justify-self: start;
  cursor: pointer; }

.btn-back p,
.btn-start p,
.btn-help p {
  margin: 0; }

.btn-back:active,
.btn-start:active,
.btn-help:active {
  filter: brightness(120%);
  transform: scale(0.95); }

.signpost {
  display: inline-block;
  color: #f8feff;
  text-align: center;
  font-family: "Fredoka One";
  background: #186fc6;
  border: 3px solid #fff;
  border-radius: 10px;
  box-shadow: 0 -2px 0 3px #3c91e6 inset, 0 2px 2px rgba(60, 145, 230, 0.17), 0 10px rgba(255, 255, 255, 0.25) inset;
  min-height: 3rem;
  line-height: 2rem;
  font-size: 1.4rem;
  padding: 1% 5%;
  margin: 2%; }

@media only screen and (max-height: 600px) {
  html {
    font-size: 12px; } }

.helper-avatar-container {
  position: absolute;
  justify-self: flex-end;
  height: 30vh;
  right: 15px;
  bottom: -8vh;
  display: flex;
  align-items: center; }

.helper-avatar {
  margin-left: 1vw;
  height: 30vh; }

.speech-box {
  background: linear-gradient(to bottom, #fef5b1 0%, #fdec6e 20%, #fcde09 80%, #e1c503 100%);
  border: solid 2px white;
  border-radius: 2em;
  min-width: 25rem;
  align-items: center;
  padding: 3%;
  position: relative; }
  .speech-box div,
  .speech-box p {
    text-align: center;
    margin: 0;
    font-family: "Amatic SC", cursive;
    font-weight: bold;
    color: #0a2e27;
    font-size: 1.5rem; }

.speech-box:after,
.speech-box:before {
  content: " ";
  height: 0;
  pointer-events: none;
  position: absolute;
  top: 3em;
  width: 0; }

.speech-box:after {
  background: #69dfc7;
  border: solid white;
  border-radius: 0 2.6em 1.6em 0;
  border-width: 0 0 2px 2px;
  height: 0.65em;
  left: 100.1%;
  top: 55%;
  margin: -0.62em 0 0 -1.5px;
  width: 0.8em; }

.speech-box:before {
  background: #fde74c;
  border: solid white;
  border-radius: 0 1.6em 1.6em 0;
  border-width: 0px 3px 2px 0px;
  height: 1em;
  top: 80%;
  left: 100.1%;
  margin: -1.3em 0 0 -1px;
  width: 0.8em; }

.background-home {
  background-image: url(/img/Background_Home.svg);
  min-height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center; }

.container-home {
  display: flex;
  flex-direction: column;
  align-items: center; }

.img-logo {
  width: 60vw;
  padding-bottom: 1%; }

.background-help {
  min-height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; }

.video-container {
  display: flex;
  flex-direction: column;
  margin: 0px auto;
  width: 60vw; }

.video-player {
  align-self: center;
  width: 100%;
  height: auto;
  border: white 8px solid;
  background-color: white; }

.btn-play {
  align-self: center; }
  .btn-play.play {
    background-color: #f69a5d;
    box-shadow: 0 -2px 0 3px #f69a5d inset, 0 5px 5px rgba(65, 8, 8, 0.17), 0 15px rgba(255, 255, 255, 0.25) inset; }

.map-background {
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; }

.place-circle {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  pointer: cursor; }
  .place-circle.unlocked p {
    background: #f69a5d;
    box-shadow: 0 -2px 0 3px #e8894a inset, 0 5px 5px rgba(65, 8, 8, 0.17), 0 15px rgba(255, 255, 255, 0.25) inset;
    border-radius: 10px;
    min-width: 15vw; }
  .place-circle.locked p {
    background: #8f8aef;
    box-shadow: 0 -2px 0 3px #6f69eb inset, 0 5px 5px rgba(65, 8, 8, 0.17), 0 15px rgba(255, 255, 255, 0.25) inset;
    border-radius: 10px;
    min-width: 15vw;
