  z-index: 1; }

.router-link a {
  display: flex;
  flex-direction: column;
  align-items: center; }

.stars svg {
  width: 3vw; }

.interaction-circle {
  height: 20vh;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  pointer: cursor; }
  .interaction-circle.unlocked p {
    background: #f69a5d;
    box-shadow: 0 -2px 0 3px #e8894a inset, 0 5px 5px rgba(65, 8, 8, 0.17), 0 15px rgba(255, 255, 255, 0.25) inset;
    border-radius: 10px;
    min-width: 15vw; }
  .interaction-circle.unlocked.completed p {
    background: #fde74c;
    box-shadow: 0 -2px 0 3px #f3d80d inset, 0 5px 5px rgba(65, 8, 8, 0.17), 0 15px rgba(255, 255, 255, 0.25) inset; }
  .interaction-circle.locked p {
    background: #8f8aef;
    box-shadow: 0 -2px 0 3px #6f69eb inset, 0 5px 5px rgba(65, 8, 8, 0.17), 0 15px rgba(255, 255, 255, 0.25) inset;
    border-radius: 10px;
    min-width: 15vw;
    border-radius: 10px; }
  .interaction-circle.locked .icon-image {
    filter: grayscale(90%); }

.interaction-circle p {
  font-family: "Fredoka One", cursive;
  text-align: center;
  font-size: 1.1rem;
  margin: 2%;
  color: #04121f;
  background-color: #f69a5d;
  border-radius: 10px;
  padding: 3%; }

.router-link a {
  display: flex;
  flex-direction: column;
  align-items: center; }

.interaction-circle:active {
