  justify-content: space-around; }

.interaction-grid {
  margin: 2% auto;
  padding: 0 !important;
  min-height: 40vh;
  width: 90vw;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  align-items: stretch;
  justify-items: center; }

.interaction-question {
