  margin: 0; }

.btn-answer {
  border: 3px solid #f69a5d;
  background: #feee7e;
  box-shadow: 0 5px 5px rgba(65, 8, 8, 0.17), 0 15px rgba(255, 255, 255, 0.25) inset;
  display: inline-block;
  color: #0a2e27;
  text-align: center;
  font-family: "Montserrat", sans-serif;
  text-decoration: none;
  cursor: pointer;
  border-radius: 20px;
  width: 100%;
  margin: 1.5vh 0;
  font-size: 1.2rem;
  padding: 0.9rem 0; }

.selected {
